@media (max-width: 991px) {
	.main {
		.container {
			width: 90%;
			margin-left: -45%;
			padding-top: 120px; }
		.slide-inner {
			min-height: 390px;
			&_1 {}
			background-image: url(../img/slides_mob/slider_1min.jpg);
			&_2 {
				background-image: url(../img/slides_mob/slider_2min.jpg); }
			&_3 {
				background-image: url(../img/slides_mob/slider_3min.jpg); }
			&_4 {
				background-image: url(../img/slides_mob/slider_4min.jpg); } }
		.carousel-indicators {
			li {
				border-color: #000;
				background-color: transparent;
				&.active {
					background-color: #000; } } }
		&-text {
			text-align: center;
			&__title {
				font-size: 23px; }
			&__descr {
				font-size: 17px; } } } }




